// SidebarAdmin.jsx
import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import logo3 from "../../assets/image/logo3.png";
import './Style/AdminSidebar.scss' // Styles for this specific sidebar

const SidebarAdmin = ({ onLogout, isOpen, onClose }) => {
  const navigate = useNavigate();

  const handleSignOut = () => {
    if (typeof onLogout === 'function') {
      onLogout();
    } else {
      localStorage.removeItem('authToken');
      localStorage.removeItem('userRole');
      localStorage.removeItem('userData');
      navigate('/login');
    }
  };

  const handleNavClick = () => {
    // Close sidebar on mobile when a nav item is clicked
    if (onClose && window.innerWidth <= 768) {
      onClose();
    }
  };

  const handleOverlayClick = (e) => {
    // Close sidebar when clicking on overlay
    if (e.target.classList.contains('admin-sidebar__overlay')) {
      onClose();
    }
  };

  return (
    <>
      {/* Overlay for mobile */}
      {isOpen && (
        <div 
          className="admin-sidebar__overlay" 
          onClick={handleOverlayClick}
          aria-hidden="true"
        />
      )}
      
      <aside className={`admin-sidebar ${isOpen ? "open" : ""}`}>
        {/* Mobile close button */}
      

        <div className="admin-sidebar__logo-section">
          <img src={logo3} alt="logo" height="50px" width="50px" />
          <h2 className="admin-sidebar__title">Admin Dashboard</h2>
        </div>
        
        <ul className="admin-sidebar__menu">
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard" 
              end 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              Invest Dashboard
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard/userManagement" 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              User Management
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard/manageBlog" 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              Manage Blog
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard/transaction" 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              Transactions
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard/withdrawals" 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              Withdrawals
            </NavLink>
          </li>
          <li className="admin-sidebar__menu-item">
            <NavLink 
              to="/adminDashboard/adminPlans" 
              className={({ isActive }) => isActive ? "active-link" : ""}
              onClick={handleNavClick}
            >
              Plans
            </NavLink>
          </li>
        </ul>
        
        <div 
          className="admin-sidebar__signout"
          onClick={handleSignOut} 
          style={{ cursor: 'pointer' }}
          tabIndex={0}
          role="button"
          onKeyDown={(e) => {
            if (e.key === 'Enter' || e.key === ' ') {
              handleSignOut();
            }
          }}
        >
          Sign Out
        </div>
      </aside>
    </>
  );
};

export default SidebarAdmin;